<template>
    <div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="">
                        <el-input v-model="form.workId" :disabled="true">{{form.workId}}</el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.fullname" >{{form.fullname}}</el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="form.vacationType" placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker
                            v-model="form.startTime"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                            placeholder="">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker
                            v-model="form.endTime"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                            placeholder="">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="form.office" placeholder="">
                            <el-option key="002" label="" value="001"></el-option>
                            <el-option key="007" label="" value="002"></el-option>
                            <el-option key="008" label="" value="003"></el-option>
                            <el-option key="009" label="" value="004"></el-option>
                            <el-option key="010" label="" value="005"></el-option>
                            <el-option key="011" label="" value="006"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input type="textarea" rows="5" v-model="form.reason"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-upload
                            class="upload-demo"
                            action="#"
                            multiple
                            :on-change="handleChange"
                            :on-remove="handleRemove"
                            :file-list="fileList"
                            :auto-upload="false">
                            <el-button slot="trigger" size="small" type="primary"></el-button>
                        </el-upload>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit"></el-button>
                        <el-button></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'vacationApply',
    data() {
        return {
            form: {
                workId: this.$store.state.user.workId,
                office: '',
                fullname: this.$store.state.user.fullname,
                vacationType: '',
                reason: '',
                startTime: '',
                endTime: '',
                file:''

            },
            fileList: []
        };
    },
    methods: {
        onSubmit() {
            let formData = new FormData()
            this.fileList.forEach(file => {
                formData.append('file', file.raw)
            })
            formData.append("vacation", JSON.stringify(this.form))
            this.fileRequest('oasys-back-end/vacationAdd',formData).then(resp=>{
                console.log(resp)
                if (resp.data===true){
                    this.$message.success('');
                    this.$router.replace("vacationList")
                }else {
                    this.$message.error("")
                }
            })


        },
        onFileSubmit(){
            this.fileRequest('oasys-back-end/resignApply',this.form.applyDoc).then(resp=>{
                if (resp){
                    this.$message.success("")
                }else {
                    this.$message.error("")
                }
            })
        },
        handlePreview(file){
            console.log("file="+file)
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleChange(file, fileList) {
            this.fileList = fileList
            console.log(fileList)
        },
    }
};
</script>



