<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                    type="primary"
                    icon="el-icon-circle-plus"
                    class="handle-del mr10"
                    @click="handleAdd()"
                >
                </el-button>
                <el-button
                    type="primary"
                    icon="el-icon-delete"
                    class="handle-del mr10"
                    @click="delAllSelection"
                >
                </el-button>
                <el-input v-model="keyword" placeholder="" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()"></el-button>
                <el-button type="primary" @click="exports()"></el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="username" label="" align="center"></el-table-column>
                <el-table-column prop="password" label="" align="center">{{'***'}}</el-table-column>
                <el-table-column label="" align="center">
                    <template slot-scope="scope">{{ scope.row.workId }}</template>
                </el-table-column>
                <el-table-column label="" align="center" prop="fullname"></el-table-column>
                <el-table-column label="" align="center">
                    <template slot-scope="scope">{{ scope.row.employee.sex }}</template>
                </el-table-column>
                <el-table-column prop="gmtCreate" label="" width="200" align="center"></el-table-column>
                <el-table-column label="" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.employee.status===''?'success':(scope.row.employee.status===''?'danger':'')"
                        >{{ scope.row.employee.status }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.$index, scope.row)"
                        >
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            @click="handleDelete(scope.$index, scope.row)"
                        >
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!--  -->
        <el-dialog title="" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="">
                    <el-input v-model="form.username"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.fullname"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.sex"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.nation"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.telephone"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.address"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false"> </el-button>
                <el-button type="primary" @click="saveEdit"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="addVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="">
                    <el-input v-model="form.username"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.workId"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.fullname"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.sex"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.nation"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.noId"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.telephone"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.address"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.employee.money"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.roleId" placeholder="">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false"> </el-button>
                <el-button type="primary" @click="saveAdd()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getRequest} from "../../util/api";

export default {
    name: "infoList",
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            keyword: null,
            tableData: [],
            multipleSelection: [],
            delList: [],
            editVisible: false,
            addVisible: false,
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            form: {
                username: "",
                password: "",
                workId: "",
                fullname: "",
                roleId: "",
                status: "",
                employee: {
                    fullname: "",
                    workId: "",
                    sex: "",
                    noId: "",
                    nation: "",
                    telephone: "",
                    address: "",
                    money: "",
                    status: ""
                }
            },
            idx: -1,
            id: -1,
            options: [{
                value: '001',
                label: ''
            }, {
                value: '002',
                label: ''
            }, {
                value: '003',
                label: ''
            }, {
                value: '004',
                label: ''
            }, {
                value: '005',
                label: ''
            },{
                value: '006',
                label: ''
            },{
                value: '007',
                label: ''
            },{
                value: '008',
                label: ''
            },{
                value: '009',
                label: ''
            },{
                value: '010',
                label: ''
            },{
                value: '011',
                label: ''
            }],
            value: ''
        };
    },
    mounted() {
        this.getData();
    },
    methods: {
        //  easy-mock 
        getData() {
            console.log(this.$store.state.user.roleId)
            let url = 'oasys-back-end/getUser?page=' + this.page + '&size=' + this.size + '&fatherRole=' + this.$store.state.user.roleId;
            if (this.keyword != null) {
                console.log("keyword="+this.keyword)
                url += '&keyword=' + this.keyword
            }
            getRequest(url).then(resp => {
                this.tableData = resp.data.data;
                console.log(this.tableData)
                this.total = resp.data.total;
                this.pageTotal = Math.ceil(this.total / this.size)
            })
        },
        // 
        handleDelete(index, row) {
            // 
            this.$confirm('', '', {
                type: 'warning'
            }).then(() => {
                let employee = row
                this.deleteRequest('oasys-back-end/deleteUser', employee).then(resp => {
                    if (resp) {
                        this.$message.success('');
                        this.tableData.splice(index, 1);
                    } else {
                        this.$message.error('');
                    }
                })
            })
                .catch(() => {
                });
        },
        // 
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
        delAllSelection() {
            const length = this.multipleSelection.length;
            let str = '';
            this.delList = this.delList.concat(this.multipleSelection);
            for (let i = 0; i < length; i++) {
                console.log(this.delList[i].workId)
                this.deleteRequest('oasys-back-end/deleteUser', this.delList[i]).then(resp => {
                    if (resp) {
                        str += this.delList[i].workId + ' ';
                    }
                })
                this.$message.error(`` + str);
                this.multipleSelection = [];
                this.getData()
            }


        },
        // 
        handleEdit(index, row) {
            this.idx = index;
            this.form = row;
            this.editVisible = true;
        },
        // 
        saveEdit() {
            this.editVisible = false;
            this.$message.success(` ${this.idx + 1} `);
            this.postRequest('oasys-back-end/editUser', this.form).then(resp => {
                if (resp) {
                    this.$set(this.tableData, this.idx, this.form);
                    this.$message("");
                    this.getData();
                } else {
                    this.$message("");
                    this.getData();
                }
            })
        },
        // 
        handleAdd() {
            this.addVisible = true;
        },
        // 
        saveAdd() {
            this.addVisible = false;
            this.form.employee.workId = this.form.workId;
            console.log(this.form.employee.workId)
            this.form.employee.fullname = this.form.fullname;
            this.postRequest('oasys-back-end/addUser', this.form).then(resp => {
                if (resp) {
                    this.$message("");
                    this.getData();
                } else {
                    this.$message("");
                    this.getData();
                }
            })

        },
        // 
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },

        //
        exports: function () {
            window.open("oasys-back-end/export?fatherRole="+this.$store.state.user.roleId+"&keyword="+this.keyword);
        }
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



